
import { _decorator, Component, Node, Vec3, NodePool, Collider2D, Contact2DType, IPhysics2DContact } from 'cc';
import { PoolObjects } from './PoolObjects';
const { ccclass, property } = _decorator;

 
@ccclass('Bullet')
export class Bullet extends PoolObjects  {
    private _height: number = 900;

    //when bullet collides with enemies
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) 
    {
        if (otherCollider.tag == 2)
        {
            //destroy bullet
            this._isHit = true;
        }
    }

    update (deltaTime: number) {
        
        // let curPos = new Vec3 (this.node.position.x, this.node.position.y, this.node.position.z);
        // curPos.y += this.speed * deltaTime;

        this.Moving(deltaTime);

        if (this._currentPos.y > this._height || this._isHit)
        {
            this.BackToPool();
        }
        //this.node.position = curPos;
       
    }
}

